<template>
    <div class=''>
        <h1>layui</h1>
        <span>(vuelayuilayuidomjQueryVue.jsdom)</span>
        <div class="layui-tab layui-tab-card">
            <ul class="layui-tab-title">
                <li class="layui-this"></li>
                <li></li>
                <li>/</li>
                <li>form</li>
                <li></li>
                <li></li>
            </ul>
            <div class="layui-tab-content" :style="{height:newHeight,'overflow':'auto'}">
                <div class="layui-tab-item layui-show">
                    <div class="layui-carousel" id="test1">
                        <div carousel-item>
                            <div><img src="/static/imgs/Lavender.jpg"></div>
                            <div><img src="/static/imgs/starry-sky.jpg"></div>
                            <div><img src="/static/imgs/starry-sky2.jpg"></div>
                            <div><img src="/static/imgs/starry-sky3.jpg"></div>
                            <div><img src="/static/imgs/starry-sky4.png"></div>
                        </div>
                    </div>
                </div>

                <div class="layui-tab-item">
                    <div class="animbox">
                        <div class="layui-anim layui-anim-rotate"><span>360</span></div>
                    </div>
                     <div class="animbox">
                        <div class="layui-anim layui-anim-fadein"><span></span></div>
                    </div>
                     <div class="animbox">
                        <div class="layui-anim layui-anim-scaleSpring"><span></span></div>
                    </div>
                    
                    <div class="animbox">
                        <div class="layui-anim layui-anim-rotate layui-anim-loop"><span></span></div>
                    </div>    
                </div>

                <div class="layui-tab-item">
                    <button type="button" class="layui-btn" @click="conso"></button>
                    <button type="button" class="layui-btn layui-btn-radius layui-btn-normal" @click="baidabtn"></button>
                    <button type="button" class="layui-btn layui-btn-sm layui-btn-radius layui-btn-danger" @click="smbtn"></button>
                    <button type="button" class="layui-btn layui-btn-xs layui-btn-radius layui-btn-warm" @click="minibtn"></button>
                </div>

                <div class="layui-tab-item">
                    <form class="layui-form" action="">
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <input type="text" name="title" required  lay-verify="required" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-inline">
                        <input type="password" name="password" required lay-verify="required" placeholder="" autocomplete="off" class="layui-input">
                        </div>
                        <div class="layui-form-mid layui-word-aux"></div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <select name="city" lay-verify="required">
                            <option value=""></option>
                            <option value="0"></option>
                            <option value="1"></option>
                            <option value="2"></option>
                            <option value="3"></option>
                            <option value="4"></option>
                        </select>
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <input type="checkbox" name="like[write]" title="">
                        <input type="checkbox" name="like[read]" title="" checked>
                        <input type="checkbox" name="like[dai]" title="">
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <input type="checkbox" name="switch" lay-skin="switch">
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <input type="radio" name="sex" value="" title="">
                        <input type="radio" name="sex" value="" title="" checked>
                        </div>
                    </div>
                    <div class="layui-form-item layui-form-text">
                        <label class="layui-form-label"></label>
                        <div class="layui-input-block">
                        <textarea name="desc" placeholder="" class="layui-textarea"></textarea>
                        </div>
                    </div>
                    <div class="layui-form-item">
                        <div class="layui-input-block">
                        <button class="layui-btn" lay-submit lay-filter="formDemo"></button>
                        <button type="reset" class="layui-btn layui-btn-primary"></button>
                        </div>
                    </div>
                    </form>
                </div>

                <div class="layui-tab-item">
                    <fieldset class="layui-elem-field layui-field-title">
                        <legend> - </legend>
                    </fieldset>
                    <ul class="flow-default" style="height: 300px;" id="LAY_demo2"></ul>
                </div>

                <div class="layui-tab-item">
                    <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
                        <legend></legend>
                    </fieldset>
                        
                    <ul class="layui-nav">
                        <li class="layui-nav-item"><a href=""></a></li>
                        <li class="layui-nav-item layui-this">
                            <a href="javascript:;"></a>
                            <dl class="layui-nav-child">
                            <dd><a href="">1</a></dd>
                            <dd><a href="">2</a></dd>
                            <dd><a href="">3</a></dd>
                            </dl>
                        </li>
                        <li class="layui-nav-item"><a href=""></a></li>
                        <li class="layui-nav-item">
                            <a href="javascript:;"></a>
                            <dl class="layui-nav-child">
                            <dd><a href=""></a></dd>
                            <dd><a href=""></a></dd>
                            <dd class="layui-this"><a href=""></a></dd>
                            <dd><a href=""></a></dd>
                            </dl>
                        </li>
                        <li class="layui-nav-item"><a href=""></a></li>
                    </ul>

                    <fieldset class="layui-elem-field layui-field-title" style="margin-top: 30px;">
                        <legend></legend>
                    </fieldset>
                    
                    <ul class="layui-nav layui-nav-tree layui-inline" lay-filter="demo" style="margin-right: 10px;">
                        <li class="layui-nav-item layui-nav-itemed">
                            <a href="javascript:;"></a>
                            <dl class="layui-nav-child">
                            <dd><a href="javascript:;"></a></dd>
                            <dd><a href="javascript:;"></a></dd>
                            <dd><a href="javascript:;"></a></dd>
                            <dd><a href=""></a></dd>
                            </dl>
                        </li>
                        <li class="layui-nav-item">
                            <a href="javascript:;"></a>
                            <dl class="layui-nav-child">
                            <dd><a href=""></a></dd>
                            <dd><a href=""></a></dd>
                            <dd><a href=""></a></dd>
                            </dl>
                        </li>
                        <li class="layui-nav-item"><a href=""></a></li>
                        <li class="layui-nav-item"><a href=""></a></li>
                    </ul>
                </div>
            </div>
        </div>
       

    </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
//import
components: {},
data() {
//
return {
    newHeight:null,
};
},
// data
computed: {},
//data
watch: {},
// - this
created() {
   
    this.newHeight=window.innerHeight-262+'px';
    window.onresize = function() {
      this.newHeight=window.innerHeight-262  +'px';
    };
},
// - DOM
mounted() {
 console.log(window.innerHeight,'height')
  //form 
  layui.form.render();
  layui.form.on('submit(formDemo)', function(data){
        layer.msg(JSON.stringify(data.field));
        return false;
    });
  

  //  
  layui.carousel.render({
    elem: '#test1'
    ,width: '100%' //
    ,arrow: 'always' //
    //,anim: 'updown' //
  });

  
  layui.element.render();
  //
  layui.element.on('nav(demo)', function(elem){
    //console.log(elem)
    layer.msg(elem.text());
  });

  
  layui.flow.load({
    elem: '#LAY_demo2' //
    ,scrollElem: '#LAY_demo2' //
    ,isAuto: false
    ,isLazyimg: true
    ,done: function(page, next){ //
      //
      setTimeout(function(){
        var lis = [];
        for(var i = 0; i < 6; i++){
          lis.push('<li><img src="/static/imgs/liuupload.jpg?v='+ ( (page-1)*6 + i + 1 ) +'"></li>')
        }
        next(lis.join(''), page < 6); // 6
      }, 500);
    }
  });
  

},
//
methods: {
    conso(){
        //layer.msg(''); 
        layer.confirm('layui',{
            anim: 1
            ,btn: ['', '', ''] //
            ,btn3: function(index, layero){
                //
                layer.msg('3')
            }
            }, function(index, layero){
            //
                layer.msg('1')
            }, function(index){
            //
                layer.msg('2')
            });
    },
    baidabtn(){
        layer.msg('', {icon: 5,anim: 3});
    },
    smbtn(){
        layer.open({
            title: ''
            ,content: 'layer'
            ,anim:6
        });
    },
    minibtn(){
        layer.tab({
            anim:2,
            area: ['600px', '300px'],
            tab: [{
                title: 'TAB1', 
                content: '1'
            }, {
                title: 'TAB2', 
                content: '2'
            }, {
                title: 'TAB3', 
                content: '3'
            }]
        });   
    }
    },
}
</script>
<style lang='scss' scoped>
//@import url(); css

.animbox{
    display:inline-block;
    width:100px;
    height:100px;
    background:lightblue;
    border-radius:50%;
    text-align:center;
    line-height:100px
}
img{
    width:100%;
    height:100%;
}
/deep/ .layui-form-selectup dl{
    top:0;
    bottom:auto;
}
/deep/ .flow-default li{
    display:inline-block;
    width:48%;
    margin:0 5px 10px 0;
    img{
        width:100%;
    }
}
</style>


