const count = {
    state:{
        count:1,
    },
    getters:{
        count:state=>state.count,
    },
    mutations:{
        INCREMENT(state) {
            state.count++
        },
        DECREMENT(state) {
            state.count--
        }
    },
    actions:{
        increment(context) {  //mutationscontextstore 
            context.commit('INCREMENT');
        },
        decrement(context) {  //
            context.commit('DECREMENT');
        },
    }
}

export default count


