<template>
  <el-header>
    <div :class="isCollapse ? 'logo min-logo':'logo' "
      style="float:left;height: 59px;line-height:59px;overflow: hidden;font-size:24px;padding-left:20px;">
      <img src="@/assets/logo.png" :alt="pageTitle">
      <span>{{pageTitle}}</span>
    </div>

    <div class="userInfo">
      <div class="user-avator"><i class="el-icon-user-solid"></i></div>
      <div style="margin-left: 10px;cursor: pointer;" @click="toUserinfo">,{{username}}</div>
      <div style="margin-left: 10px;cursor: pointer;">|</div>
      <div style="margin-left: 10px;cursor: pointer;" @click="toChangePwd"></div>
      <div style="margin-left: 10px;cursor: pointer;">|</div>
      <div style="margin-left: 10px;cursor: pointer;" @click="loginOut"></div>
    </div>
  </el-header>

</template>

<script>
//jsjsjson
//import  from '';

import { UserModule } from "@/api/common";
export default {
  //import
  components: {},
  data () {
    //
    return {
      username: sessionStorage.getItem('userName'),
      roleName: sessionStorage.getItem('roleName'),
      pageTitle: "",
      isCollapse: false,
      changeBarDirection: false
    };
  },
  // data
  computed: {},
  //data
  watch: {

  },
  // - this
  created () {

  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    toUserinfo () {
      this.$router.push('/systemManagement/userinfo')
    },
    toChangePwd () {
      this.$router.push('/systemManagement/changePwd')
    },
    loginOut () {

      UserModule.getUserLogout().then(res => {
        if (res.data) {

          sessionStorage.setItem('userId', '');
          sessionStorage.setItem('userName', '');
          sessionStorage.setItem('roleId', '');
          sessionStorage.setItem('roleName', '');
          sessionStorage.setItem('kitchenId', '');
          sessionStorage.setItem('kitchenName', '');
          localStorage.setItem('roleAction', '');
          localStorage.setItem('authorization', '');

          this.$router.push('../login');
        }
      });
    }
  },
}
</script>
<style lang='scss' scoped>
//@import url(); css
.userInfo {
  float: right;
  margin-right: 20px;
  line-height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo {
  width: 700px;
  transition: width 0.5s linear;
  img {
    height: 38px;
    vertical-align: middle;
  }
  span {
    vertical-align: middle;
  }
}
.min-logo {
  width: 60px;
  transition: width 0.3s;
}
.user-avator {
  margin-left: 20px;
}
.user-avator img {
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.el-header {
  width: 100%;
  height: 60px;
  padding: 0;
  margin: 0;
  color: #666666;
  text-align: left;
}
</style>



