<template>
  <div class='bread_container'
       style="display:flex; justify-content:space-between;"
       id="bread_container">
    <div style="width: 80%;">
      <span @click="handleLefeMenu"
            class="bars">
        <i :class="isCollapse ? 'el-icon-s-unfold':'el-icon-s-fold' "></i>
      </span>
      <el-breadcrumb class="breadcrumb"
                     separator="/">
        <el-breadcrumb-item v-for='(title,index) in matchedArr'
                            :key='index'>
          {{title}}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data () {
    //
    return {
      isCollapse: false,
    };
  },
  // data
  computed: {
    matchedArr () {
      let temp = [], temps = [];
      this.$route.matched.filter((item, index, self) => {
        if (item.meta.title) {
          const title = item.meta.title;
          temp.push(title);
        }
      });
      temp.filter((item, index, self) => {
        if (!temps.includes(item)) {
          temps.push(item);
        }
      })
      return temps;
    }
  },
  //data
  watch: {},
  // - this
  created () {

  },
  // - DOM
  mounted () {

  },
  //
  methods: {
    handleLefeMenu () {
      this.$store.dispatch('setLeftCollapse');  // 
      this.$store.dispatch('handleLeftMenu');  //  180->35/35-180
      this.isCollapse = !this.isCollapse;
    },

  },
}
</script>
<style lang='scss' scoped>
//@import url(); css
.bread_container {
  background-color: rgb(229, 229, 229);
  width: 100%;
  display: flex;
  align-items: center;
  .bars {
    float: left;
    margin: 5px 10px;
    cursor: pointer;
  }
  .breadcrumb {
    height: 30px;
    line-height: 30px;
    .breadbutton {
      float: left;
      margin: 4px 5px 0 0;
    }
  }
  .el-button {
    padding: 4px 10px !important;
  }
}
</style>


