<template>
  <div class="home">
    <el-row style="margin-top: 15px;" :gutter="20">
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <el-card class="box-card" shadow="hover">
            <div class="turnover"><i class="el-icon-circle-check"></i>{{todayDate}}</div>
          </el-card>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { debounce } from "@/utils/tool";
import iTable from "@/components/table";
import SearchForm from "@/components/searchForm";

import { OrderModule } from "@/api/common";

let echarts = require("echarts/lib/echarts");
// 
require("echarts/lib/chart/pie");
// 
require("echarts/lib/component/title");
require("echarts/lib/component/tooltip");
require("echarts/lib/component/legend");
require("echarts/lib/component/legendScroll");

export default {
  name: "home",
  components: {
    iTable,
    SearchForm,
  },
  data() {
    return {
      todaySummarySum: "",
      todaySummaryCount: "",
      thisWeekSummaryPer: "",
      nextWeekSummaryPer: "",
      mealStudentCount: [],
      mealOrderToCount: [],
      menuStudentSelected: 0,
      menuOrderToSelected: 0,
      todayDate: "",
      dateTypeArr: {
        day: "",
        week: "7",
        month: "30",
      },
      studentTypeArr: {
        STUDENT: "",
        STAFF: "",
      },
      paidArr: {
        paid: "",
        unpaid: "",
      },
      dealMethodArr: {
        CABINET: "",
        CANTEEN: "",
      },
      mealTypeArr: {
        BREAKFAST: "",
        LUNCH: "",
        AFTERNOON_TEA: "",
        DINNER: "",
        SUPPER: "",
      },

      mealStudentOption: {
        tooltip: {
          trigger: "item",
          /* formatter: "{a} <br/>{b} : ({c}) {d}%"*/
          formatter: "{a} {b} : {c} {d}%",
        },
        legend: {
          left: "center",
          top: "bottom",
          itemWidth: 10,
          itemHeight: 10,
          selectedMode: false, //
          textStyle: {
            fontSize: 12,
            color: "#ffffff",
          },
          formatter: function (name) {
            //
            return name.length > 4 ? name.slice(0, 4) + "..." : name;
          },
          data: [],
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "60%"],
            center: ["50%", "40%"],
            avoidLabelOverlap: false,
            hoverAnimation: true, // hover 
            selectedMode: false, //'single''multiple'
            selectedOffset: 0, //
            label: {
              show: false,
              position: "center",
              formatter: "{d}%",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "12",
                fontWeight: "bold",
                color: "#ffffff",
              },
            },
            labelLine: {
              show: false,
            },
            data: [],
          },
        ],
        color: ["#f5cd6e", "#d87480"], //
      },
      mealTimeOption: {
        tooltip: {
          trigger: "item",
          /* formatter: "{a} <br/>{b} : ({c}) {d}%"*/
          formatter: "{a} {b} : {c} {d}%",
        },
        legend: {
          left: "center",
          top: "bottom",
          itemWidth: 10,
          itemHeight: 10,
          selectedMode: false, //
          textStyle: {
            fontSize: 12,
            color: "#ffffff",
          },
          formatter: function (name) {
            //
            return name.length > 4 ? name.slice(0, 4) + "..." : name;
          },
          data: [],
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: [0, "60%"],
            center: ["50%", "40%"],
            hoverAnimation: true, // hover 
            selectedMode: false, //'single''multiple'
            selectedOffset: 0, //
            data: [],

            itemStyle: {
              normal: {
                label: {
                  show: true,
                  textStyle: {
                    fontSize: 12,
                  },
                  position: "inner",
                },
                labelLine: {
                  show: false,
                },
              },
            },
          },
        ],
        color: ["#6db5f8", "#82d06b", "#7673ec", "#ec7062"], //
      },
      mealOrderToOption: {
        tooltip: {
          trigger: "item",
          /* formatter: "{a} <br/>{b} : ({c}) {d}%"*/
          formatter: "{a} {b} : {c} {d}%",
        },
        legend: {
          left: "center",
          top: "bottom",
          itemWidth: 10,
          itemHeight: 10,
          selectedMode: false, //
          textStyle: {
            fontSize: 12,
            color: "#ffffff",
          },
          formatter: function (name) {
            //
            return name.length > 4 ? name.slice(0, 4) + "..." : name;
          },
          data: [],
        },
        series: [
          {
            name: "",
            type: "pie",
            radius: ["50%", "60%"],
            center: ["50%", "40%"],
            avoidLabelOverlap: false,
            hoverAnimation: true, // hover 
            selectedMode: false, //'single''multiple'
            selectedOffset: 0, //
            label: {
              show: false,
              position: "center",
              formatter: "{d}%",
            },
            labelLine: {
              show: false,
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "12",
                fontWeight: "bold",
              },
            },
            data: [],
          },
        ],
        color: ["#81d06a", "#7774ed"], //
      },

      dataShow: "",
    };
  },
  methods: {
    formatAmount(val) {
      var str = ((val * 100) / 100).toFixed(2) + "";
      var intSum = str
        .substring(0, str.indexOf("."))
        .replace(/\B(?=(?:\d{3})+$)/g, ","); //
      var dot = str.substring(str.length, str.indexOf(".")); //
      var ret = intSum + dot;
      return ret;
    },
    formatNumber(str) {
      var intSum = str.toString().replace(/\B(?=(?:\d{3})+$)/g, ","); //
      return intSum;
    },
    getHomeSummary() {
      let datas = {};
      OrderModule.getHomeSummary(datas).then((res) => {
        if (res.data) {
          this.todayDate = res.data.todayDate;
        }
      });
    },
  },
  mounted() {
    this.getHomeSummary();
  },
};
</script>
<style lang="" scoped>
.el-card {
  border: 0;
}

.home .turnover {
  text-align: center;
  color: #666666;
}
.el-form-item {
  margin-bottom: 0;
}
.home .grid-content /deep/ .el-card__header {
  padding: 12px 20px;
  font-size: 13px;
}
.home .turnover /deep/ .el-card {
  margin-bottom: 10px;
}
.home .grid-content /deep/ .el-card__body {
  padding: 300px 0;
}
.tableCom /deep/ .el-table {
  font-size: 12px !important;
}
.tableCom /deep/ .el-table th {
  background: linear-gradient(rgb(247, 245, 245) 0%, rgb(229, 229, 229) 100%);
  color: rgb(51, 51, 51);
  height: 35px;
  line-height: 35px;
  padding: 0px;
}
.tableCom /deep/ .el-table td {
  color: rgb(51, 51, 51);
  height: 35px;
  line-height: 35px;
  padding: 0px;
}
.home /deep/ .el-pagination {
  margin-top: 10px;
  font-size: 13px !important;
}

.echarts-content {
  width: 100%;
}

.echarts-content .chart-main {
  width: 100%;
  position: relative;
  overflow: hidden;
  -webkit-transition: height 0.6s;
  -moz-transition: height 0.6s;
  -o-transition: height 0.6s;
  transition: height 0.6s;
}

.block-menu {
  height: 30px;
  display: flex;
}

.block-menu .menu-item {
  color: #ffffff;
  display: inline-block;
  text-align: center;
  line-height: 30px;
  cursor: pointer;
  width: 25%;
}
.block-menu .on {
  background: #cccccc;
}
.dataTotal {
  margin-top: 15px;
  text-align: center;
  padding: 5px;
  border: 1px solid #eee;
  font-size: 12px;
}
.dataTotal .num {
  color: #f56c6c;
  font-size: 16px;
  padding: 5px 0;
}
.dataTotal .left {
  padding: 5px 0;
}
.dataTotal .left i {
  color: #409eff;
  font-size: 14px;
  margin-left: 3px;
}
.dataTotal .el-col {
  border-right: 4px solid #409eff;
}
</style>


