<template>
  <div :class="theme">
    <router-view></router-view>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  components: {},
  data() {
    //
    return {
      theme: "contentMain light",
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  // - this
  created() {},
  // - DOM
  mounted() {},
  //
  methods: {
    setTheme(theme) {
      console.log(theme);
      if (theme == "dark") {
        this.theme = "contentMain dark";
      } else {
        this.theme = "contentMain light";
      }
    },
  },
};
</script>
<style lang='scss' scoped>
//@import url(); css
// .contentMain {
// height:100%;
//overflow: auto;
// }
.contentMain {
  padding-bottom: 20px;
  height: auto;
}

.light {
  background: #ffffff;
}

.dark {
  background: #122845;
}
</style>


